<script>
export default {
  name: "MainContainer",
  data() {
    return {
      companyName: "",
      name: "",
      mail: "",
      contents: "",
      scrollY: 0,
    };
  },
  methods: {
    scroll(target) {
      const element = document.getElementById(target);
      if (element) {
        const position = element.getBoundingClientRect().top + this.scrollY;
        window.scrollTo({
          top: position,
          behavior: "smooth",
        });
      }
    },
    getScroll() {
      this.scrollY = window.scrollY;
    },
  },
  mounted() {
    window.addEventListener("scroll", this.getScroll);
  },
  computed: {
    isSend() {
      if (
        this.companyName != "" &&
        this.name != "" &&
        this.mail != "" &&
        this.contents != ""
      ) {
        return true;
      } else {
        return false;
      }
    },
  },
};
</script>


